  .Title {
    text-align: center;
  }

  .Title-header {
    background-color:rgb(9, 9, 95);
    height: 150px;
    padding: 20px;
    color: white;
  }

  .Title-title {
    font-size: 1.5em;
  }

  .Title-intro {
    font-size: medium;
  }

  .App {
    text-align: center;
  }
  
  .App-logo {
    -webkit-animation: App-logo-spin infinite 1s linear;
            animation: App-logo-spin infinite 1s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 120px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  


